"use client";

import React from 'react';

import {useState} from 'react';

import { Save, X } from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { DateTimePicker } from '@/components/ui/datetimepicker';

// import { useIndexedDB } from "react-indexed-db-hook";

import { useIndexedDB } from "@/lib/hooks/useIndexedDB";
import { DBConfig } from "@/lib/data/dbconfig";

import { Invoice } from '@/lib/models/invoices';

import { 
    selectFile,
    selectFileName,
    selectData,
 } from '@/lib/features/document_ocr/document.slice'

import { useAppDispatch, useAppSelector } from "@/lib/hooks/hooks";
import moment from 'moment';

export function InvoiceLayout() {

    const {
        putValue,
        getValue,
        getAllValue,
        updateValue,
        deleteValue,
        isDBConnecting,
      } = useIndexedDB(Database.name, [Database.userTable]);

    const [formData, setFormData] = useState({
        filename: "",
        type: "",
        vendor: "",
        place: "",
        item: "",
        item_type: "",
        qty: "",
        amount: "",
      });

    const uploadedFile = useAppSelector(selectFile);
    const fileName = useAppSelector(selectFileName);
    const data = useAppSelector(selectData);

    const [date, setDate] = useState(moment(data.date, "DD/MM/YYYY hh:mm:ss").toDate());
    const [checkintime, setCheckinTime] = useState(moment(data.checkin_time, "DD/MM/YYYY hh:mm:ss").toDate());
    const [checkouttime, setCheckoutTime] = useState(moment(data.checkout_time, "DD/MM/YYYY hh:mm:ss").toDate());

    const handleInputChange = (e: any) => {
        const { name, value } = e.target;
        setFormData((prevFormData) => ({
            ...prevFormData,
            [name]: value,
        }));
    };

    const setNewDate = (newDate: any) => {
        setDate(newDate);
      }

    const setNewCheckinTime = (newCheckinTime: any) => {
        setCheckinTime(newCheckinTime);
    }

    const setNewCheckoutTime = (newCheckoutTime: any) => {
        setCheckoutTime(newCheckoutTime);
    }

    const handleSubmit = () => {
        add({ name: "name", email: "email" }).then(
            (event) => {
                //console.log("ID Generated: ", event.target.result);
            },
            (error) => {
                //console.log(error);
            },
        );
    };

    return(
        <div>
            {
                (() => {
                    if(data.type == "receipt") {
                            return (
                                <b>Receipt</b>
                            )
                        } else {
                            return (
                                <b>Undefined</b>
                            )
                        }
                })()  
            }  is found in the image.

            <form className="flex items-center w-full justify-between" onSubmit={handleSubmit}>
                <div className="grid grid-flow-col gap-4 mt-3">
                    <div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Vendor</label> : &nbsp;
                                <Input type="text" 
                                    onChange={handleInputChange}
                                    value={data.vendor}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Place</label> : &nbsp;
                                <Input type="text" 
                                    onChange={handleInputChange}
                                    value={data.place}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Item</label> : &nbsp;
                                <Input type="text" 
                                    onChange={handleInputChange}
                                    value={data.item}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Item Type</label> : &nbsp;
                                <Input type="text" 
                                    onChange={handleInputChange}
                                    value={data.item_type}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Qty</label> : &nbsp;
                                <Input type="text" 
                                    onChange={handleInputChange}
                                    value={data.qty}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>
                        
                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Date</label> : &nbsp;
                                <DateTimePicker size={35} value={date} timeDisplay={false} onChange={setNewDate}/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Check-in Time</label> : &nbsp;
                                <DateTimePicker size={35} value={checkintime} timeDisplay={true} onChange={setNewCheckinTime}/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Check-out Time</label> : &nbsp;
                                <DateTimePicker size={35} value={checkouttime} timeDisplay={true} onChange={setNewCheckoutTime}/>
                            </div>
                        </div>

                        <div className="mt-2">
                            <div className="flex flex-row items-center">
                                <label className="basis-1/5">Parking Fees</label> : &nbsp;
                                <Input type="text" 
                                    onChange={handleInputChange}
                                    value={data.amount}
                                    className="basis-2/5 text-gray-500 font-medium text-sm bg-gray-100 file:cursor-pointer cursor-pointer file:border-0 file:py-2 file:px-4 file:mr-4 file:bg-gray-800 file:hover:bg-gray-700 file:text-white rounded"/>
                            </div>
                        </div>

                        <div className="mt-6 pl-80">
                            <div className="flex flex-row items-center">
                                <Button type='submit' size="sm" variant="default" className="h-8 gap-1">
                                    <Save className="h-4 w-4" />
                                    <span className="sr-only sm:not-sr-only sm:whitespace-nowrap">
                                        Save
                                    </span>
                                </Button>
                                
                                <Button size="sm" variant="outline" className="h-8 gap-1 ml-2">
                                    <X className="h-4 w-4" />
                                    <span className="sr-only sm:not-sr-only sm:whitespace-nowrap">
                                        Cancel
                                    </span>
                                </Button>
                            </div>
                        </div>
                    </div>
                    <div><h5>{fileName}</h5><img src={uploadedFile} className='w-3/6 h-fill' /></div>
                </div>
            </form>
        </div>
    )

    
}